.header {
  height: 4em;
  max-width: 1500px;
  margin: 0 auto;
  margin-bottom: 5rem;
  padding: 0 1rem;
  justify-content: space-between;
}

.header h3 {
  z-index: 1;
  transition: transform 0.3s ease;
}

.header h3:hover {
  transform: scale(1.1);
}

@media (max-width: 600px) {
  .header {
    height: 6em;
  }
}